/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/

html {
  box-sizing: border-box;
  font-size: 1rem;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: $color__background-body; /* Fallback for when there is no custom background color defined. */
}

blockquote,
q {
  quotes: '' '';

  &:before,
  &:after {
    content: '';
  }
}

hr {
  border: 1px dotted $color__background-hr;
  // margin-bottom: $base__space-unit;
}

ul,
ol {
  margin: 0 0 $base__space-unit ($base__space-unit * 2);
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: $base__space-unit;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 $base__space-unit $base__space-unit;
}

img {
  height: auto; /* Make sure images are scaled correctly. */
  max-width: 100%; /* Adhere to container width. */
}

table {
  margin: 0 0 $base__space-unit;
  width: 100%;
}

// Custom Styling

// Header

.quote-title-tag {
  a {
    color: $color__text-main;
    &:visited {
      color: $color__text-main;
    }
  }
  span {
    color: $color__text-screen;
    font-family: 'Exo light-italic';
  }
  @include desktop {
    text-align: center;
    font-size: 3.5rem;
  }
}

// Content

.quote-text {
  p {
    font-family: $font__main;
  }
}

.author-area p, {
  font-size: 1.5rem;
  display: inline;
}

.author-area {
  margin-bottom: 40px;
}

.show-more-btn-wrapper {
  @include desktop {
    text-align: center;
  }
}

// Quotation Icons
.content-area .fa-quote-right,
.content-area .fa-quote-left {
  display: none;
  @include desktop {
    display: inline-block;
    color: $color__text-screen;
    font-size: 6rem;
  }
 
}





